.container {
  width: 115px;
  height: 115px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: border-bottom 0.2s ease-in-out;
}
@media (max-width: 1350px) {
  .container {
    width: calc(65px + 50 * (100vw - 320px) / 1030);
  }
}
@media (max-width: 1350px) {
  .container {
    height: calc(65px + 50 * (100vw - 320px) / 1030);
  }
}

.quizNumber {
  width: 68px;
  height: 68px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.2s ease-in-out;
}
@media (max-width: 1350px) {
  .quizNumber {
    width: calc(52px + 16 * (100vw - 320px) / 1030);
  }
}
@media (max-width: 1350px) {
  .quizNumber {
    height: calc(52px + 16 * (100vw - 320px) / 1030);
  }
}

.quizNumberText {
  font-size: 48px;
  color: white;
  font-weight: bold;
}
@media (max-width: 1350px) {
  .quizNumberText {
    font-size: calc(26px + 22 * (100vw - 320px) / 1030);
  }
}

