.app {
  padding-top: 64px;
  padding-right: 50px;
  padding-left: 50px;
  padding-bottom: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1350px) {
  .app {
    padding-top: calc(44px + 20 * (100vw - 320px) / 1030);
  }
}
@media (max-width: 1350px) {
  .app {
    padding-right: calc(30px + 20 * (100vw - 320px) / 1030);
  }
}
@media (max-width: 1350px) {
  .app {
    padding-left: calc(30px + 20 * (100vw - 320px) / 1030);
  }
}
@media (max-width: 1350px) {
  .app {
    padding-bottom: calc(40px + 20 * (100vw - 320px) / 1030);
  }
}

.appContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

