@import '../../mixins/adaptiveMixin';

.container {
  @include adaptive("width", 115, 65, 1);
  @include adaptive("height", 115, 65, 1);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: border-bottom .2s ease-in-out;
}

.quizNumber {
  @include adaptive("width", 68, 52, 1);
  @include adaptive("height", 68, 52, 1);
  border-radius:  50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color .2s ease-in-out;
}

.quizNumberText {
  @include adaptive("font-size",48, 26, 1);
  color:  white;
  font-weight: bold;
}