@import './mixins/adaptiveMixin';

.app {
  @include adaptive("padding-top", 64, 44, 1);
  @include adaptive("padding-right", 50, 30, 1);
  @include adaptive("padding-left", 50, 30, 1);
  @include adaptive("padding-bottom", 60, 40, 1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.appContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
